import React, {Component} from 'react';
import s from './header.module.scss';
import Background from '../img/bg/me.jpg';

const headerImg = {
  backgroundImage: "url(" + Background + ")",
  backgroundSize: 'cover'
}
const topDescr = {
  heading: 'Hello! My name is Anrew Khrystoiev',
  text: 'I am a Front-End developer, I have experience in this field for more than two years. I work with technologies such as: HTML5, CSS3, Java Script (ES5, ES6), JQuery, React.js, JSON, SASS, SCSS, Bootstrap 3/4, Gulp, Webpack, Twig, Responsive layout (mobile devices adaptation). I am able to work with website content using site management systems: WordPress, Opencart. I am able to test sites using all popular browsers, as well as the X-Code program. At the moment I am developing towards React.js, in future plans I want to master the frameworks Angular and Vue.js. This portfolio was created using React.js and the CRA (Create React App) module with routing implementation.'
}

class Header extends Component {
  constructor () {
    super();
    this.state = {
      text: "Hello! My name is Anrew Khrystoiev",
      print: '',
      isHide: false
    }
    this.moveBottom = this.moveBottom.bind(this);
  }

  componentWillMount () {
    this.writeText()
  }

  writeText () {
    const formatedText = this.state.text.split('');
    let print = '';
    const loop = setInterval(() => {
      if (formatedText.length >= 1) {
        if (formatedText[0] === '1') {
          print += '<mark>'
          formatedText.shift()
        } else if (formatedText[0] === '2') {
          print += '<mark>'
          formatedText.shift()
        } else {
          print += formatedText.shift()
          this.setState({ print })
        }
      } else { clearInterval(loop) }
    }, 90)
  }

  moveBottom() {
    const sh = window.innerHeight - 60;
    window.scrollTo({
      top: sh,
      behavior: "smooth"
    });
    this.setState({
      isHide: !this.state.isHide
    })
  }

  render () {
    const {isHide} = this.state;
    return (
      <header className={s.pageHeader} style= { headerImg }>
        <div className="container">
          <div className={s.content}>
            <div className={s.content__inner}>
              <h1 className={`${s.mainHeading} lololo`} dangerouslySetInnerHTML={{__html:this.state.print}}></h1>
              <div className={s.descrText}>
                <p>{topDescr.text}</p>
              </div>
            </div>
          </div>
        </div>
        <span className={`${s.btnDown} ${isHide ? `${s.hide}` : ''}`} onClick={this.moveBottom}></span>
      </header>
    );
  }
}

export default Header;