const workSlides = arraySlides.map( (slide) => {
	return(
		<div className={s.workSlide} key={slide.id}>
			<div className={s.slideLeft}>
				<a className={`${s.slideLink} btnDefault`} href={slide.link} target="_blank" rel="noopener noreferrer">Перейти</a>
				<img src={slide.img} alt={slide.name} />
			</div>
			<div className={s.slideRight}>
				<h4 className={s.slideHeading}>{slide.name}</h4>
				<p className={s.slideDescr}>{slide.descr}</p>
				<h6>{fields.technologies}:</h6>
				<p>{slide.technologies}</p>
			</div>
		</div>
	)
} );

class Works extends React.Component {
	render() {
	    const workSliderParams = {
	      modules: [Pagination, Navigation],
	      effect: 'cube',
	      grabCursor: true,
	      loop: true,
	      cubeEffect: {
	        shadow: true,
	        slideShadows: true,
	        shadowOffset: 20,
	        shadowScale: 0.4
	      },
	      pagination: {
	        el: '.swiper-pagination.workPagination',
	        clickable: true
	      },
	      navigation: {
	        nextEl: '.swiper-button-next.workSwiperNext',
	        prevEl: '.swiper-button-prev.workSwiperPrev'
	      }
	    };
		return(
			<section className="pageSection">
				<div className="container">
					<h3 className="heading text-left">{fields.heading}</h3>
					<div className={s.worksSliderWrap}>
						<Swiper {...workSliderParams}>
					    	{workSlides}
					    </Swiper>
					</div>
				</div>
			</section>
		)
	}
}

export default Works;