import React, {Component} from 'react';
import ScrollAnimation from 'react-animate-on-scroll';
import { Parallax } from 'react-parallax';
import "animate.css/animate.min.css";
import s from './contact.module.scss';

class Contact extends Component {
	render() {
		return(
			<Parallax bgImage={require('../img/bg/typing.jpg')} blur={{ min: -1, max: 4 }} strength={300}>
				<section className={`${s.contactSection} pageSection`}>
					<div className="container">
						<h3 className="heading text-left">Send me message</h3>
						<ScrollAnimation animateIn="flipInY">
							<form className={s.contactForm} id="fs-frm" name="simple-contact-form" acceptCharset="utf-8" action="https://formspree.io/andrehrist1991@gmail.com" method="post">
							  <fieldset id="fs-frm-inputs">
							    <label>Name</label>
							    <input className={s.textField} type="text" name="name" id="full-name" placeholder="Name Lastname" required="" />
							    <label>Email</label>
							    <input className={s.textField} type="email" name="_replyto" id="email-address" placeholder="email@example.com" required="" />
							    <label>Message</label>
							    <textarea className={s.textField} rows="5" name="message" id="message" required=""></textarea>
							    <input type="hidden" name="_subject" id="email-subject" value="Contact Form Submission" />
							  </fieldset>
							  <input className={`${s.send} btnSuccess`} type="submit" value="Send" />
							</form>
						</ScrollAnimation>
					</div>
				</section>
			</Parallax>
		)
	}
}

export default Contact;