import React, {Component} from 'react';
import {NavLink} from "react-router-dom";
import socialNetworks from '../data/socialnetworks';
import s from './navigation.module.scss';

const nameFields = {
  "logo": "Andrew Khrystoiev"
}

class Navigation extends Component {
  constructor(props) {
    super(props)
    this.state = {
      isExpanded: false
    }
    this.moveToAnchor = this.moveToAnchor.bind(this);
  }

  componentDidMount () {
    this._bindScroll();
  }
  componentWillUnmount () {
    this._unbindScroll()
  }
  _bindScroll = () => {
    window.addEventListener(
      'scroll',
      this._handleScroll
    )
  }
  _unbindScroll = () => {
    window.removeEventListener('scroll', this._handleScroll)
  }
  _handleScroll = () => {
    const top = document.documentElement.scrollTop
      || document.body.parentNode.scrollTop
      || document.body.scrollTop
    if (top < 200) {
      this._navigation.className = s.navigation
    }
    else {
      this._navigation.className = `${s.navigation} ${s.fixed}`
    }
  }

  handleToggle(e) {
    this.setState({
      isExpanded: !this.state.isExpanded
    })
    document.querySelector("body").classList.toggle("addOverflow");
  }

  moveToAnchor() {
    this.setState({
      isExpanded: !this.state.isExpanded
    })
    const sh = window.innerHeight - 60;
    window.scrollTo({
      top: sh,
      behavior: "smooth"
    });
  }

  

  render () {
    const {isExpanded} = this.state;
    const menus = [
      {
        name: "Experience",
        patch: "/experience"
      },
      {
        name: "Skills",
        patch: "/technologies"
      }
    ];
    const navLinks = menus.map( (menu) => {
      return (
        <li key={menu.name.toString()}><NavLink to={menu.patch} activeClassName={s.active} onClick={this.moveToAnchor}>{menu.name}</NavLink></li>
      )
    });
    const socialLinks = socialNetworks.map((item) => {
      return (
        <li key={item.id}>
          <a href={item.link} target="_blank" rel="noopener noreferrer" className={s.socialLnk}>{item.icon}</a>
        </li>
      )
    });
    
    
    return ( <div className={`${s.navigation}`} ref={(ref) => {

          this._navigation = ref

        }}>
        <div className="container">
          <div className={s.content}>
            <div className="left">
              <h2 className={s.logo}>{nameFields.logo}</h2>
            </div>
            <div className={s.right}>
              <nav>
                <div className={s.socialWrap}>
                  <ul className={s.socialList}>
                    {socialLinks}
                  </ul>
                </div>
                <ul className={`${s.menu} ${isExpanded ? `${s.show}` : ''}`}>
                  {navLinks}
                </ul>
                <span className={`${s.menuToggle} ${isExpanded ? `${s.active}` : ''}`} onClick={(e) => this.handleToggle(e)}>
                  <span className={`${s.mMt} ${s.one}`}></span>
                  <span className={`${s.mMt} ${s.two}`}></span>
                  <span className={`${s.mMt} ${s.three}`}></span>
                  <span className={`${s.mMt} ${s.four}`}></span>
                </span>
              </nav>
            </div>
          </div>
        </div>
      </div>
    )
  }
}

export default Navigation;
